.default-background {
  background-color: #EFF4E4;
}
.exploreheader {
  width: 100%;
  height: 4.6656298600311%;
  text-align: left;
  position: fixed;
  top: 0;
  left: 0;
  background-color: #344146;
  margin: 0;
  z-index: 50;
  display: table;
  padding: 0;
}
.exploreheader a {
  font-family: 'Lora', serif;
  font-size: 1.5em;
  float: left;
  padding: 1% 1%;
  color: white;
  line-height: 1.5em;
  text-decoration: none;
}
.searchtext {
  width: 20%;
  height: 50%;
  border-radius: 5px;
  border: 0;
  float: right;
  margin: 1%;
  padding: 1%;
  background-color: white;
  font-family: 'Lora', serif;
  font-size: 1em;
}
.searchtext:focus {
  width: 40%;
  height: 50%;
  box-shadow: 0 0 5px 1px white;
  animation: textboxanimation 0.5s;
}
@keyframes textboxanimation {
  from {
    border: 0;
    width: 20%;
  }
  to {
    box-shadow: 0 0 5px 1px white;
    width: 40%;
  }
}
.content {
  position: absolute;
  top: 15%;
}
.entry {
  background-color: white;
  padding: 1%;
  animation: fadeIn 1s;
  font-family: 'Lora', serif;
  font-size: 1em;
  font-weight: 400;
  line-height: 1.5em;
  color: #888888;
  margin-bottom: 1%;
  border-radius: 5px;
  border: 1px solid #91CED7;
}
@keyframes fadeIn {
  from {
    opacity: 0.5;
  }
  to {
    opacity: 1;
  }
}
.entry a {
  margin: 1% 1% 1% 1%;
  padding: 0px;
  color: #344146;
  text-decoration: none;
}
.entry a:hover {
  text-decoration: underline;
}
.entry p {
  margin: 1% 1% 1% 1%;
  padding: 0px;
}
.article {
  margin: 0px;
  padding: 0px;
  background-color: white;
  animation: fadeIn 1s;
  font-family: 'Lora', serif;
  font-size: 20px;
  font-weight: 400;
  line-height: 1.5em;
  color: #888888;
  float: left;
  max-width: 100%;
  display: block;
}
.article h1 {
  font-size: 25px;
  padding: 0 60px;
}
.summary {
  font-style: italic;
}
.article p {
  padding: 0 60px;
}
.article li {
  line-height: 1.5em;
  height: auto;
  padding: 0 40px;
  margin: 0 40px;
}
.code {
  font-family: "Courier New", Courier, monospace;
  background-color: #F0F1F5;
  margin: 30px 60px;
  padding: 20px 0;
}
.code p {
  padding: 0 20px;
}
.githubcode {
  padding: 0 60px;
}
